import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import useSWR from 'swr';
import { employeesAPI, fetcher, swrKeys } from '../../services/api.jsx';
import { useAuth } from '../../contexts/AuthContext.jsx';
import {
  Container,
  Typography,
  Button,
  Card,
  CardContent,
  Grid,
  Box,
  Chip,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress
} from '@mui/material';
import { Add as AddIcon, Edit as EditIcon, Delete as DeleteIcon } from '@mui/icons-material';

const EmployeeList = () => {
  const [openDialog, setOpenDialog] = useState(false);
  const [editingEmployee, setEditingEmployee] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    designation: '',
    employee_status: 'application_received'
  });
  const [error, setError] = useState('');
  const { isAuthenticated } = useAuth();

  // Fetch employees using SWR only if authenticated
  const { data: employeesResponse, error: fetchError, mutate } = useSWR(
    isAuthenticated ? swrKeys.employees : null, 
    fetcher
  );

  // Extract employees array from the paginated response
  const employees = employeesResponse?.results || [];

  const handleOpenDialog = (employee = null) => {
    if (!isAuthenticated) {
      setError('Please log in to manage employees.');
      return;
    }
    
    if (employee) {
      setEditingEmployee(employee);
      setFormData({
        name: employee.name,
        email: employee.email,
        designation: employee.designation,
        employee_status: employee.employee_status
      });
    } else {
      setEditingEmployee(null);
      setFormData({
        name: '',
        email: '',
        designation: '',
        employee_status: 'application_received'
      });
    }
    setOpenDialog(true);
    setError('');
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setEditingEmployee(null);
    setFormData({
      name: '',
      email: '',
      designation: '',
      employee_status: 'application_received'
    });
    setError('');
  };

  const handleSubmit = async () => {
    try {
      if (editingEmployee) {
        await employeesAPI.update(editingEmployee.id, formData);
      } else {
        await employeesAPI.create(formData);
      }
      mutate(); // Revalidate data
      handleCloseDialog();
    } catch (error) {
      setError(error.message);
    }
  };

  const handleDelete = async (employeeId) => {
    if (window.confirm('Are you sure you want to delete this employee?')) {
      try {
        await employeesAPI.delete(employeeId);
        mutate(); // Revalidate data
      } catch (error) {
        setError(error.message);
      }
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      'application_received': 'default',
      'interview_scheduled': 'warning',
      'hired': 'success',
      'not_accepted': 'error'
    };
    return colors[status] || 'default';
  };

  // Show authentication message if not logged in
  if (!isAuthenticated) {
    return (
      <Container>
        <Box sx={{ textAlign: 'center', mt: 4 }}>
          <Typography variant="h4" component="h1" gutterBottom>
            Employees
          </Typography>
          <Alert severity="info" sx={{ mb: 3 }}>
            Please log in to view and manage employees.
          </Alert>
          <Button
            component={Link}
            to="/login"
            variant="contained"
            color="primary"
            size="large"
            sx={{ mr: 2 }}
          >
            Sign In
          </Button>
          <Button
            component={Link}
            to="/register"
            variant="outlined"
            color="primary"
            size="large"
          >
            Create Account
          </Button>
        </Box>
      </Container>
    );
  }

  if (fetchError) {
    return (
      <Container>
        <Alert severity="error">Failed to load employees: {fetchError.message}</Alert>
      </Container>
    );
  }

  if (!employees) {
    return (
      <Container sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Container>
    );
  }

  return (
    <Container>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          Employees
        </Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => handleOpenDialog()}
        >
          Add Employee
        </Button>
      </Box>

      {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}

      <Grid container spacing={3}>
        {employees.map((employee) => (
          <Grid item xs={12} sm={6} md={4} key={employee.id}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                  <Typography variant="h6" component="h2" gutterBottom>
                    {employee.name}
                  </Typography>
                  <Box>
                    <IconButton
                      size="small"
                      onClick={() => handleOpenDialog(employee)}
                    >
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      size="small"
                      color="error"
                      onClick={() => handleDelete(employee.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                </Box>
                
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  {employee.email}
                </Typography>
                
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  {employee.designation}
                </Typography>
                
                <Box sx={{ mt: 2 }}>
                  <Chip
                    label={employee.employee_status.replace('_', ' ').toUpperCase()}
                    color={getStatusColor(employee.employee_status)}
                    size="small"
                    sx={{ mr: 1, mb: 1 }}
                  />
                  <Chip
                    label={employee.company_name}
                    color="primary"
                    variant="outlined"
                    size="small"
                    sx={{ mr: 1, mb: 1 }}
                  />
                  <Chip
                    label={employee.department_name}
                    color="secondary"
                    variant="outlined"
                    size="small"
                    sx={{ mb: 1 }}
                  />
                </Box>

                <Box sx={{ mt: 2 }}>
                  <Button
                    component={Link}
                    to={`/employees/${employee.id}`}
                    variant="outlined"
                    size="small"
                    fullWidth
                  >
                    View Details
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* Add/Edit Dialog */}
      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>
          {editingEmployee ? 'Edit Employee' : 'Add Employee'}
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Name"
            fullWidth
            variant="outlined"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            sx={{ mt: 2 }}
          />
          <TextField
            margin="dense"
            label="Email"
            type="email"
            fullWidth
            variant="outlined"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            sx={{ mt: 2 }}
          />
          <TextField
            margin="dense"
            label="Designation"
            fullWidth
            variant="outlined"
            value={formData.designation}
            onChange={(e) => setFormData({ ...formData, designation: e.target.value })}
            sx={{ mt: 2 }}
          />
          <FormControl fullWidth sx={{ mt: 2 }}>
            <InputLabel>Status</InputLabel>
            <Select
              value={formData.employee_status}
              label="Status"
              onChange={(e) => setFormData({ ...formData, employee_status: e.target.value })}
            >
              <MenuItem value="application_received">Application Received</MenuItem>
              <MenuItem value="interview_scheduled">Interview Scheduled</MenuItem>
              <MenuItem value="hired">Hired</MenuItem>
              <MenuItem value="not_accepted">Not Accepted</MenuItem>
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingEmployee ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default EmployeeList;
